@import "../base-components";

.woocommerce-checkout {
	.woocommerce {
		padding: pxToRem(40) 0;
	}
	.woocommerce-billing-fields__field-wrapper {
		display: flex;
		flex-wrap: wrap;
		margin: 0 pxToRem(-8);
		.form-row {
			flex: 0 0 50%;
			padding: 0 pxToRem(8);
		}
		.form-row-wide {
			flex: 0 0 100%;
		}
	}
}

.woocommerce-form-coupon-toggle {
	margin-bottom: pxToRem(20);
}

.woocommerce-form-coupon {
	margin-top: pxToRem(10);
	margin-bottom: pxToRem(40);
	.button {
		@include button-base;
		@include button-size-small;
		@include button-solid;
	}
	@include media-breakpoint-up(md) {
		max-width: calc(50% - 15px);
	}
}

#place_order {
	@include button-base;
	@include button-size-small;
	@include button-solid;
}

.wc_payment_methods {
	margin: pxToRem(20) 0;
	list-style-type: none;
	padding: 0;
}

.woocommerce-checkout-review-order {
	@include media-breakpoint-up(md) {
		max-width: calc(50% - 15px);
	}
}

.shop_table {
	thead {
		.product-name {
			text-align: left;
		}
		.product-total {
			text-align: right;
		}
	}
	.cart-subtotal,
	.order-total {
		th,
		td {
			text-align: right;
			padding-top: 0;
			padding-bottom: 0;
		}
	}
	.product-total {
		text-align: right;
	}
	@include media-breakpoint-down(md) {
		thead {
			display: block;
			border-bottom: 1px solid var(--cl-border);
			.product-name,
			.product-total {
				font-size: pxToRem(16);
			}
		}
		tfoot {
			width: 100%;
			display: block;
			padding: pxToRem(10) pxToRem(0);
		}
		tbody {
			tr {
				border-bottom: 1px solid var(--cl-border) !important;
			}
		}
		.product-name {
			flex: 0 0 70%;
			max-width: 70%;
			order: 0;
		}
		.product-total {
			flex: 0 0 30%;
			max-width: 30%;
			text-align: right;
		}
	}
}
